<template>
    <div>
      <h3>推薦關鍵字： {{ keyword }}</h3>
      <table>
        <thead>
            <tr>
                <th>選取</th>
                <th>關鍵字</th>
                <th >刪除</th>
            </tr>
        </thead>
        <tbody>
          <tr v-for="recommended in recommendKeywords_list" :key="recommended">
            <td><input type="checkbox" v-model="selectedColumns[recommended]"></td>
            <td>{{ recommended }}</td>
            <td><button @click="removeKeywordrequest(recommended)">Delete</button></td>
          </tr>
        </tbody>
      </table>
      <button class="submit-button" @click="SubmitRecommend" >上傳同義詞庫</button>
    </div>
  </template>
  
  <script>
  const web_api_url = 'http://127.0.0.1:5000';
  export default {
    data(){
        return{
            selectedColumns:{}
        }
    },
    props: {
      keyword: String,
      recommendKeywords_list: Array
    },
    methods: {
      removeKeywordrequest(keyword) {
        this.$emit('update-value', keyword);
      },
      async SubmitRecommend() {
        this.selectedColumnsArray = Object.keys(this.selectedColumns).filter(column => this.selectedColumns[column]);
  
          if (this.selectedColumnsArray.length == 0) {
            alert("請選擇1或1以上的欄位數量");
            return;
          }  
          // 使用FormData來上傳文件和列名
          const formData = new FormData();
          formData.append('keyword', this.keyword);
          // console.log("selectedColumnsArray Names:", this.selectedColumnsArray);
          formData.append('synonyms', this.selectedColumnsArray);
          // console.log("formData", formData);
          // 開始上傳
          try {
            const response = await fetch(web_api_url+'/synonyms_write/', { // 替換為你的服務器端點
              method: 'POST',
              body: formData
            });
            this.selectedColumns = {};
            if (response.ok) {
              alert('文件上傳成功！');
              // console.log(response); // 處理来自服務器的響應
            } else {
              alert('文件上傳失敗:' + response.message);
            }
          } catch (error) {
            console.error("上傳錯誤:", error);
            alert('上傳過程中出現錯誤，請重試！');
          }
      }
    }
  };
  </script>

<style scoped>
table {
  margin-top: 10px;
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 10px;
  width: 500px;
  margin-left: auto;
  margin-right: auto;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
}

th {
  background-color: #f2f2f2;
}
</style>